import { Component, OnInit } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { MenuPage } from '../menu/menu.page';
import { IonSlides,NavController, NavParams } from '@ionic/angular';

@Component({
  selector: 'app-productdetail',
  templateUrl: './productdetail.page.html',
  styleUrls: ['./productdetail.page.scss'],
})
export class ProductdetailPage implements OnInit {
  slideOptions = {
    initialSlide: 1,
    speed: 2000,
    pagination : {
      el: '.swiper-pagination',
      clickable: true
    },
  };
  slideOptsTwo = {
    initialSlide: 0,
    slidesPerView: 3,
    autoplay: true,
    spaceBetween: 20,
  };
  constructor(private popoverController:PopoverController) { }

  slidesDidLoad(slides: IonSlides) {
    slides.startAutoplay();
  }

  colorSelected:any="RAW";
  bgColorSel:any="#ddd";
  ngOnInit() {
  }

  next(slides: IonSlides) {
    slides.slideNext();
  }

  prev(slides: IonSlides) {
    slides.slidePrev();
  }

  SlideDidChange(object, slideView) {

  }

  openOption()
  {
    document.getElementById('color_list').style.height="auto";
  }

  colorSelector()
  {
    document.getElementById('color_list').style.height="0";
  }

  openOptionSize()
  {
    document.getElementById('sizeList_list').style.height="auto";
  }

  sizeSelector()
  {
    document.getElementById('sizeList_list').style.height="0";
  }
 
  async menuOpen()
  {
    const popover = await this.popoverController.create({
      component: MenuPage,
      cssClass: 'menuList',
    });
    await popover.present();
  }
}
